export default {
	props: {
		shopInfo: {
			type: Object,
			default: () => {}
		}
	},
	methods: {
		deliveryTime(time) {
			let date = new Date();
			date.setMinutes(date.getMinutes() + time);
			return date.getHours() + ':' + date.getMinutes();
		}
	}
};
